@-webkit-keyframes opacity{
    from{
        opacity: 0;
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to{
        opacity: 1;
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
    }

@keyframes opacity{
    from{
        opacity: 0;
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to{
        opacity: 1;
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
    }

.form-input-control{
    margin: 10px;
}

.form-btn{
    display: inline-flex;
    align-items: center;
    height: 30px;
    margin-top: 10px;
}

.close-btn{
    margin-left: -85px;
}

.submit-btn{
    margin-left: 10px;
    position: absolute;
}

.loader-container, .done-container, .error-container{
    align-content: center;
    display: flex;
    margin-top: 10px;
    text-align: center;
    flex-wrap: wrap;
    height: 250px;
    justify-content: center;
}

.process{
    display: block;
    height: 35px;
    width: 100%;
}

.loader{
    background-image: url(/static/media/loader-blue.2c0c5093.gif);
    background-repeat: no-repeat;
    background-size: 100px;
    display: block;
    height: 100px;
    margin-top: 30px;
    width: 25%;
}

.done{
    -webkit-animation: opacity;
            animation: opacity;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 100px;
    background-position-x: center;
    height: 100px;
    width: 100%;
}

.error{
    -webkit-animation: opacity;
            animation: opacity;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    background-image: url(/static/media/error-cross.020410ef.png);
    background-repeat: no-repeat;
    background-size: 100px;
    background-position-x: center;
    height: 100px;
    width: 100%;
}
@supports((position: -webkit-sticky) or (position: sticky)){
    .header{
      position: -webkit-sticky;
      position: sticky;
      top: 0;
    }
  }

.header{
    background-color: transparent;
    width: 100%;
}

.header-content{
    display: flex;
    align-items: center;
}

.logo{
    margin: 0 30px -10px 35px;
    width: 125px;
}

.register{
    padding-top: 15px;
    position: absolute;
    right: 80px;
    text-align: center;
    width: 10%;
}

.register button{
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 18px;
    position: relative;
}

.register button:before{
    content: "";
    background-color: #48CAE4;
    height: 1px;
    bottom: 0;
    position: absolute;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: all 0.15s ease-in-out;
    width: 100%;
}

.register button:hover:before{
    visibility: visible;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

.division{
    border: none;
    background-color: #90E0EF!important;
    margin: 10px auto 50px auto!important;
    height: 1px;
    text-align: center;
    opacity: 1!important;
    width: 80%;
}

.ReactModal__Overlay {
    height: 100%;
    opacity: 0;
    transition: all 400ms ease-in-out;
}

.ReactModal__Overlay--after-open {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
}
  
.ReactModal__Overlay--before-close {
    opacity: 0;
}

.ReactModal__Content{
    background: #CAF0F8!important;
    border: 1px solid #90E0EF!important;
    border-radius: 5px; 
    height: -webkit-max-content; 
    height: max-content;
    left: 30%!important;
    opacity: 0;
    padding-bottom: 40px!important;
    padding-top: 30px!important;
    text-align: center;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
    transition: all 350ms ease-in-out;    box-shadow: 0 1px 2px 0 rgba(0,0,0,.1);
    top: 25%!important;
    width: 40%;
}

.ReactModal__Content--after-open {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
  
.ReactModal__Content--before-close {
    opacity: 0;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}
@-webkit-keyframes fade-in {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes fade-in {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@-webkit-keyframes github-spin-in{
    from {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}

@keyframes github-spin-in{
    from {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
    to {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
}

@-webkit-keyframes github-spin-out{
    0% {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
}

@keyframes github-spin-out{
    0% {
        -webkit-transform: rotateY(360deg);
                transform: rotateY(360deg);
    }
    100% {
        -webkit-transform: rotateY(0deg);
                transform: rotateY(0deg);
    }
}

.home{
    display: block;
    padding: 30px 80px 30px 80px;
}

.welcome{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.welcome h1, .welcome h3, .welcome p{
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    flex-wrap: wrap;
    margin-bottom: 50px;
    text-align: center;
    width: 60%;
}

.welcome h1{
    font-size: 60px;
    text-align: center;
    width: 50%;
}

.welcome h3{
    font-size: 30px;
}

.welcome p{
    font-size: 25px;
    height: -webkit-max-content;
    height: max-content;
}

.sign{
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    height: auto;
    margin: -35px 30% 0 30%!important;
    width: 22%;
}

.github-logo{
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: github-spin-out;
            animation-name: github-spin-out;
    bottom: -235px;
    fill: gray;
    position: absolute;
    right: 20px;
    width: 4%;
    transition: 0.7s ease-in-out
}

.github-logo-out{
    fill: gray;
    width: 100%;
}

.github-logo:hover{
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: github-spin-in;
            animation-name: github-spin-in;
    fill: #00B4D8;
    transition: 0.7s ease-in-out;
}
